export default {
  data() {
    return {
      list: []
    }
  },
  methods: {
    getList() {
      this.request(this.api.AddressList).then(res => {
        if (res.code == 200) {
          this.list = res.data
        }
      })
    }
  },
  created() {
    this.getList()
  }
}



